  margin: 15px;
}
.container .row [class^=col-] {
  min-height: 100px;
  padding: 0 15px;
}
@media (min-width: 400px) and (max-width: 799px) {
  .container .row [class^=col-] {
    flex-basis: 100%;
  }
}
@media (min-width: 800px) and (max-width: 1023px) {
  .container .row [class^=col-] {
    flex-basis: 100%;
  }
}
@media (max-width: 399px) {
  .container .row [class^=col-] {
    flex-basis: 100%;
  }
}

.col-1 {
