/*region belka górna nawigacji*/
.header {
  position: sticky;
  top: 0;
  z-index: 100;
  width: 100%;
  height: 70px;
  background: #FFFFFF;
  box-shadow: 0 3px 6px #00000029;
  opacity: 1;
  margin: 0;
}
.header .container .row {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@media (min-width: 800px) and (max-width: 1023px) {
  .header .container .row {
    margin-right: 15px;
  }
}

.header-logo a {
  font-family: "Raleway", sans-serif;
  font-weight: 300;
  color: #08A6E4;
  font-size: 37px;
  text-decoration: none;
}

.menu {
  display: flex;
  flex-direction: row;
}

.menu-list {
  list-style: none;
  display: flex;
  justify-content: center;
  margin-right: 15px;
}

.menu-list a {
  text-decoration: none;
  margin-left: 75px;
  padding: 0;
  text-transform: uppercase;
  font-family: "Open Sans", sans-serif;
  font-size: 11px;
  letter-spacing: 3.28px;
  color: #A5A5A5;
  text-align: center;
  opacity: 1;
  transition: 0.5s;
  white-space: nowrap;
}
@media (min-width: 800px) and (max-width: 1023px) {
  .menu-list a {
    margin-left: 50px;
  }
}
@media (min-width: 400px) and (max-width: 799px) {
  .menu-list a {
    display: none;
  }
}
@media (max-width: 399px) {
  .menu-list a {
    display: none;
  }
}

.menu-list a:hover {
  font-weight: bold;
  color: #000000;
  opacity: 1;
  position: relative;
}

.menu-list a:hover::after {
  position: absolute;
  content: "";
  bottom: -27px;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: #08A6E4;
  opacity: 1;
}

