  display: flex;
  justify-content: center;
  align-items: flex-start;
  margin-top: 50px;
}

.contact-info {
  width: 50%;
  height: 350px;
  text-align: left;
  font-family: "Open Sans", sans-serif;
  font-size: 16px;
  color: #000000;
  letter-spacing: 0;
}

.contact-title {
  text-align: left;
  font-family: "Raleway", sans-serif;
  font-size: 49px;
  color: #000000;
  letter-spacing: 0;
}

.contact-contact {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-direction: row;
}

.contact-email {
  margin-right: 20px;
  text-align: left;
}

.contact-email,
.contact-tel {
  text-align: left;
  font-size: 16px;
  font-weight: 600;
  font-family: "Open Sans", sans-serif;
  letter-spacing: 0;
  color: #A5A5A5;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 10px;
}

.contact-email::before {
  content: url(../assets/Mail_Icon.svg);
  padding: 5px;
}

.contact-tel::before {
  content: url(../assets/Phone_Icon.svg);
  padding: 5px;
}

.form {
  flex-wrap: wrap;
  width: 50%;
  height: 350px;
}

.form-row {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  flex-direction: column;
  text-align: left;
  font-family: "Open Sans", sans-serif;
  font-size: 11px;
  font-weight: bold;
  letter-spacing: 3.28px;
  color: #08A6E4;
  opacity: 1;
}

.form-row-checkbox {
  font-size: 12px;
  font-weight: 300;
  letter-spacing: 0;
  color: #707070;
}

input[type=text],
input[type=email] {
  outline: none;
  border: none;
  border-bottom: 1px solid #A5A5A5;
  line-height: 22px;
  margin-bottom: 5px;
  width: 400px;
  height: 30px;
}

input[type=text]:focus,
input[type=email]:focus {
  border-bottom: 3px solid #08A6E4;
}

.checkbox {
  display: flex;
  position: relative;
}

.form-row-last {
  align-items: flex-end;
}

.form-button {
  border: none;
  background: #08A6E4;
  color: #FFFFFF;
  padding: 10px 50px;
  border-radius: 35px;
  transition: 0.5s;
  cursor: pointer;
  margin: 40px 20px;
}

.form-button:hover,
.form-button:focus {
  box-shadow: 10px 10px 14px rgba(7, 172, 230, 0.16);
}

.checkbox-input {
  appearance: none;
  position: absolute;
  top: 0;
  left: 0;
  height: 25px;
  width: 25px;
  border: 1px solid #A5A5A5;
  border-radius: 4px;
  /*border: 5px solid red;*/
}

.checkbox-input:checked {
  appearance: none;
  background-image: url(../assets/Checkmark.svg);
  background-position: center;
  background-size: 50%;
  background-repeat: no-repeat;
  border: 1px solid #A5A5A5;
  border-radius: 4px;
}

.checkbox-text {
  line-height: 20px;
  text-align: justify;
  margin: 0 40px;
}

/*endregion sekcja kontakt*/
/*region sekcja stopka*/
.footer {
  width: 100%;
  height: 89px;
  background: #F7F7F7;
  box-shadow: 0 -5px 40px #00000029;
}

.footer .container {
  padding-top: 20px;
}

.footer-logo {
  text-align: left;
  font-family: "Raleway", sans-serif;
  font-size: 26px;
  font-weight: 300;
  letter-spacing: 0;
  color: #08A6E4;
  opacity: 1;
}

.footer-copyrights {
  text-align: left;
  font-family: "Open Sans", sans-serif;
  font-size: 11px;
  font-weight: 300;
  letter-spacing: 0;
  color: #A5A5A5;
  opacity: 1;
  padding: 5px 0;
}

.footer-menu {
  /*border: 5px solid yellow;*/
}

.footer-menu-list {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  list-style: none;
  padding: 0;
  margin: 0;
}

.footer-menu-list li {
  padding: 10px;
}

/*endregion sekcja stopka*/